* {
  margin: 0;
  padding: 0;
}

*, *::before, *::after {
    box-sizing: border-box;
}

body {
    line-height: 1.5rem;
}

a, a:active {
    text-decoration: none;
}

p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
}

button {
    cursor: pointer;
}

input, button {
    font: inherit;
}

ol, ul {
    list-style: none;
}